.choices {
  position: relative;
  cursor: pointer;
}

.choices:focus {
  outline: none;
}

.choices::after {
  display: none;
}

.choices > .dropdown-menu {
  width: 100%;
}

.choices > .dropdown-menu .form-control {
  width: calc(100% - 0.75rem * 2);
  margin: 0.375rem 0.75rem;
}

.choices > .dropdown-menu .dropdown-item {
  padding-left: 0.75rem;
  padding-right: 0.75rem;
}

.choices > .dropdown-menu .dropdown-item.is-highlighted {
  color: #FFFFFF;
}

.choices > .form-control-sm + .dropdown-menu,
.choices > .form-select-sm + .dropdown-menu {
  min-width: 100%;
  width: auto;
}

.choices > .form-control-sm + .dropdown-menu .dropdown-item,
.choices > .form-select-sm + .dropdown-menu .dropdown-item {
  padding-right: 0.5rem;
  padding-left: 0.5rem;
  font-size: 0.8125rem;
}

.choices > .form-control-sm + .dropdown-menu .form-control,
.choices > .form-select-sm + .dropdown-menu .form-control {
  margin-right: 0.5rem;
  margin-left: 0.5rem;
}

.choices__placeholder {
  color: #95AAC9;
  opacity: 1;
}

.choices.is-open > .form-control,
.choices.is-open > .form-select {
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
}

.choices.is-open > .form-control + .dropdown-menu,
.choices.is-open > .form-select + .dropdown-menu {
  margin-top: -1px;
  border-top-right-radius: 0;
  border-top-left-radius: 0;
}

.choices__list--multiple + .form-control {
  display: inline-block;
  min-height: 0;
  width: auto;
  padding: 0;
  border-width: 0;
  border-radius: 0;
  background-color: transparent;
  font-size: 0.9375rem;
}

.form-control-lg + .choices__list--multiple + .form-control,
.form-select-lg + .choices__list--multiple + .form-control {
  font-size: 0.9375rem;
}

.form-control-sm + .choices__list--multiple + .form-control,
.form-select-sm + .choices__list--multiple + .form-control {
  font-size: 0.8125rem;
}

.choices__list--multiple {
  display: inline;
}

.choices__list--multiple > .choices__item {
  display: inline-block;
  margin-right: 0.2rem;
  padding-right: 0.375rem;
  padding-left: 0.375rem;
  font-size: 0.8125rem;
  background-color: #152E4D;
  border-radius: 0.1875rem;
}

.choices__list--multiple > .choices__item > .choices__button {
  width: 0.5rem;
  margin-left: 0.5rem;
  padding: 0;
  outline: none;
  border-width: 0;
  background-color: transparent;
  background-repeat: no-repeat;
  background-position: center center;
  background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24' fill='none' stroke='%23FFFFFF' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'%3e%3cline x1='18' y1='6' x2='6' y2='18'%3e%3c/line%3e%3cline x1='6' y1='6' x2='18' y2='18'%3e%3c/line%3e%3c/svg%3e");
  background-size: 100% auto;
  text-indent: -9999px;
  opacity: 0.5;
  transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}

.choices__list--multiple > .choices__item:hover > .choices__button {
  opacity: 1;
}

.dropzone {
  position: relative;
  display: flex;
  flex-direction: column;
}

.dz-message {
  padding: 5rem 1rem;
  background-color: #1E3A5C;
  border: 1px dashed #1E3A5C;
  border-radius: 0.375rem;
  text-align: center;
  color: #6E84A3;
  transition: all 0.2s ease-in-out;
  order: -1;
  cursor: pointer;
  z-index: 999;
}
.dz-message:hover {
  border-color: #6E84A3;
  color: #FFFFFF;
}

.dz-button {
  background: none;
  border: 0;
  font-size: inherit;
  color: inherit;
}

.dz-drag-hover .dz-message {
  border-color: #2C7BE5;
  color: #2C7BE5;
}

.dropzone-multiple .dz-message {
  padding-top: 2rem;
  padding-bottom: 2rem;
}

.dropzone-single.dz-max-files-reached .dz-message {
  background-color: rgba(18, 38, 63, 0.9);
  color: white;
  opacity: 0;
}
.dropzone-single.dz-max-files-reached .dz-message:hover {
  opacity: 1;
}

.dz-preview-single {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  border-radius: 0.375rem;
}

.dz-preview-cover {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  border-radius: 0.375rem;
}

.dz-preview-img {
  object-fit: cover;
  width: 100%;
  height: 100%;
  border-radius: 0.375rem;
}

.dz-preview-multiple .list-group-item:last-child {
  padding-bottom: 0;
  border-bottom: 0;
}

[data-dz-size] strong {
